import {UserModel} from "../../model/user";
const  userModel = new UserModel()
import my_ticket from "../../components/my_ticket/my_ticket"
export default {
  data() {
    return {
        cur_hd:1,
        list1:[],
        list2:[],
        list3:[],
        list4:[],
    };
  },
  components:{
      my_ticket
  },
  methods: {
      clickTap(val){
          console.log(val)
          this.cur_hd=val
          this.getUserCoupons(val)
      },
      getUserCoupons(status){
          let params = {
              confirm : '',
              status : status
          }
          userModel.getUserCoupons(params).then((res) => {
              switch(status){
                  case 1:
                      this.list1 = res.data
                      break;
                  case 2:
                      this.list2 = res.data
                      break;
                  case 3:
                      this.list3 = res.data
                      break;
                  case 4:
                      this.list4 = res.data
                      break;
              }
              
          })
      }
  },
  components: {},
  onLoad() {
      this.getUserCoupons(1)
  },
  onShow() {},
  onHide() {}
};
